import React from 'react';

export default function KeyPadComponent(props){
	return(
		<div className="button">
			<button name='(' tabIndex='-1'
							onClick={e=> props.onClick(e.target.name)}>(</button>
			<button name=')' tabIndex='-1'
							onClick={e=> props.onClick(e.target.name)}>)</button>
			<button name='Backspace' className='width2 Backspace'
							onClick={e=> props.onClick(e.target.name)}
							tabIndex='-1'>‚Ü§</button>

			<button name='%' tabIndex='-1'
						  onClick={e=> props.onClick(e.target.name)}>%</button>
			<button name='*' tabIndex='-1' className='asterisk'
							onClick={e=> props.onClick(e.target.name)}>*</button>
			<button name='/' tabIndex='-1' className='dash'
							onClick={e=> props.onClick(e.target.name)}>/</button>
			<button name='C' tabIndex='-1'
							onClick={e=> props.onClick(e.target.name)}>C</button>

			<button name='7' tabIndex='-1'
							onClick={e=> props.onClick(e.target.name)}>7</button>
			<button name='8' tabIndex='-1'
							onClick={e=> props.onClick(e.target.name)}>8</button>
			<button name='9' tabIndex='-1'
							onClick={e=> props.onClick(e.target.name)}>9</button>
			<button name='-'  tabIndex='-1' className='minus'
							onClick={e=> props.onClick(e.target.name)}>-</button>
			
			<button name='4' tabIndex='-1'
							onClick={e=> props.onClick(e.target.name)}>4</button>
			<button name='5' tabIndex='-1'
							onClick={e=> props.onClick(e.target.name)}>5</button>
			<button name='6' tabIndex='-1'
							onClick={e=> props.onClick(e.target.name)}>6</button>
			<button name='+'  tabIndex='-1' className='plus'
							onClick={e=> props.onClick(e.target.name)}>+</button>
			
			<button name='1' tabIndex='-1'
							onClick={e=> props.onClick(e.target.name)}>1</button>
			<button name='2' tabIndex='-1'
							onClick={e=> props.onClick(e.target.name)}>2</button>
			<button name='3' tabIndex='-1'
							onClick={e=> props.onClick(e.target.name)}>3</button>
			<button name='='  tabIndex='-1' className='height2 equals'
							onClick={e=> props.onClick(e.target.name)}>=</button>
			<button name='0'  tabIndex='-1' className='width2'
							onClick={e=> props.onClick(e.target.name)}>0</button>
			<button name='.'  tabIndex='-1' className='dot'
							onClick={e=> props.onClick(e.target.name)}>.</button>
		</div>
	);
}